.form-program {
  border: none;
  display: flex;
  padding: 0;
  margin: 0 0 26px 20px;

  @media (min-width: $tablet-width) {
    margin: 0 0 75px 70px;
    border: 1px solid $special_light_grey;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    border: 1px solid $special_light_grey;
  }
}
